@import url("https://fonts.googleapis.com/css2?family=Raleway&display=swap");
h1,
p {
  text-align: center;
  font-family: "Raleway", sans-serif;
  padding-top: 20px;
  margin-bottom: 5px;
}

p {
  margin-top: 8px;
  padding-top: 0;
}

#line {
  margin: auto;
  height: 0.5px;
  width: 45px;
  background-color: rgb(254, 95, 85);
}

.contact-links {
  margin: 20px auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.contact-links > div {
  display: flex;
  margin: 5px 0 5px 600px;
  border-radius: 5px;
  width: 300px;
  border: 1px solid black;
}
.contact-links #icon {
  background-color: rgb(162, 166, 255);
  width: 15%;
  border-radius: 5px;
}
.contact-links #details {
  background-color: rgb(254, 95, 85);
  width: 85%;
  color: white;
  padding: 5px;
  border-radius: 5px;
}/*# sourceMappingURL=Contact.css.map */